import uniCountDown from "@/components/uni-ui/uni-count-down/uni-count-down.vue"
import { mapState,mapMutations } from "vuex"
export default {
	components:{
		uniCountDown
	},
	data() {
		return {
			codeState:"获取验证码",
			phone:"",
			code:"",
			serverCode:"",
			isRefresh:true,
			minute:0
		}
	},
	methods: {
		...mapMutations(['login']),
		getCode(){
			if(!(/^1[34578]\d{9}$/.test(this.phone))){
			        uni.showToast({
			        	title:"请填写真实电话号码!",
						icon:"none"
			        }) 
			        return false; 
			    }else{
					if(this.isRefresh){
						this.isRefresh = false
						this.$R.post("/users/user_get_code/",{phone:this.phone}).then(res=>{
							if(res.result=="true"){
								this.codeState = "发送成功"
							}
							this.minute = 5
							setTimeout(()=>{
								this.isRefresh = true
								this.codeState = "获取验证码"
							},61000)
						})
					}else{
						uni.showToast({
							title:"请稍后点击!",
							icon:"none"
						})
					}
					
				}
			
		},
		validation(){
			if(!(/^\d{6}$/.test(this.code))){
			        uni.showToast({
			        	title:"请输入收到短信验证码!",
						icon:"none"
			        }) 
			        return false; 
			    }else{
					let data  = this.userinfo.data
					data["phone"] = this.phone
					data["code"] = this.code
					console.log(data)
					this.$R.post("/users/verify_sms/",data).then(res=>{
						let userfo = {}
						userfo = this.userinfo
						console.log(res)
						if(res.token){
							userfo["token"]=res.token
						}
						userfo["callback"]=()=>{
							uni.switchTab({
								url:"../my/my"
							})
					
						}
					
						this.login(userfo)
					})
				}
		}
	},
	computed:{
		...mapState({
			userinfo:state=>state.user.userInfo
		})
	}
}
